.Navbar {
    display: flex;
    justify-content: space-around;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 1rem;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
  }
  
  .Navbar a {
    color: #ffffff;
    text-decoration: none;
    font-size: 1rem;
  }
  
  .Navbar a:hover {
    color: #cccccc;
  }

  