.tic-tac-toe {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .board {
    display: flex;
    flex-direction: column;
  }
  
  .board-row {
    display: flex;
    flex-direction: row;
  }
  
  .square {
    width: 100px;
    height: 100px;
    background-color: #f0f0f0;
    border: 1px solid #000;
    font-size: 24px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .row {
    display: flex;
  }
  
  .cell {
    width: 100px;
    height: 100px;
    border: 1px solid rgb(181, 175, 175);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    cursor: pointer;
  }
  
  