@import './fonts.css';

.App {
  text-align: center;
  
}

.main-content {
  background-image: url('https://coolbackgrounds.io/images/backgrounds/index/compute-ea4c57a4.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.welcome-section {
  padding: 100px 0;
  color: #ffffff;
  font-family: 'ShareTechMono';
}

.about-section,
.contact-section {
  padding: 400px 0;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.7);
}

.about-section p {
  font-size: 20px;
}

.logo {
  width: 40px;
  height: 40px;
  margin-right: 8px;
}

.contact-container {
  background-color: rgba(50, 50, 50, 0.8); 
  padding: 20px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

a:visited {
  color: inherit;
}

