@import './fonts.css';

.ApiPage {
    text-align: center;
    
  }

  .main-content {
    background-image: url('https://coolbackgrounds.io/images/backgrounds/index/compute-ea4c57a4.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .welcome-section {
    padding: 100px 0;
    color: #ffffff;
  }

  .welcome-container {
    background-color: rgba(50, 50, 50, 0.8); 
    padding: 20px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .explain-section {
    background-color: rgba(50, 50, 50, 0.8); 
    padding: 20px;
    border-radius: 8px;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .explain-title {
    font-family: 'ShareTechMono';
    color:#ffffff;
  }

  .explanation {
    font-family: 'ShareTechMono';
    color:#ffffff;
    font-size: 1.5rem;
    
  }

  .spacer {
    height: 300px; 
  }

  .home-button {
    font-size: 24px;
    padding: 20px 40px;
    background-color: rgba(77, 122, 200 0.7); /* Dark, slightly transparent background */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .home-button:hover {
    background-color: rgba(0, 0, 0, 0.9); /* Darker background on hover */
  }


  .ApiPage .dropdown-section {
    cursor: pointer;
    border: 1px solid #ccc;
    border-color: rgb(77, 122, 200);
    border-radius: 5px;
    margin: 1rem 0;
    background-color: rgba(0, 0, 0, 0.7);
    
  }
  
  .dropdown-title {
    background-color: #000000;
    padding: 0.5rem 1rem;
    font-size: 1.25rem;
    font-weight: bold;
    opacity: 0.5;
    color: #ffffff;
    font-family: 'ShareTechMono';
    
  }
  
  .dropdown-content {
    padding: 1rem;
  }

   select,
   input[type="text"] {
   background-color: rgba(48, 74, 154, 0.4);
   color: white;
   font-family: 'ShareTechMono', monospace;
   border: none;
  padding: 0.5rem;
   margin: 0.2rem 0;
   height: 2rem;
 }

  button {
  background-color: rgba(48, 74, 154, 0.4);
  color: white;
  font-family: 'ShareTechMono', monospace;
  border: none;
  padding: 0.5rem 1rem;
  margin: 0.2rem 0;
  cursor: pointer;
  transition: 0.3s background-color;
}

  button:hover {
  background-color: rgba(0, 0, 0, 1);
}

  form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

  select {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  font-family: 'ShareTechMono', monospace;
  border: none;
  padding: 0.5rem;
  margin: 0.5rem 0;
  height: 2rem;
  margin-right: 0;
}

 .language-dropdown {
  margin-right: 2rem;
  margin-bottom: 1.5rem;
  
}

 .text-input {
  margin-right: 1rem;
}

 .buttons {
  margin-left: 1rem;
}

 .info-icon {
  width: 24px;
  height: 24px;
  margin-left: 5rem;
  cursor: pointer;
  
}

 .tooltip {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

 .tooltip-text {
  visibility: hidden;
  width: 200px;
  background-color: #000;
  color: #fff;
  text-align: center;
  padding: 5px;
  border-radius: 5px;
  position: absolute;
  z-index: 1;
  bottom: 125%; /* Position above the icon */
  left: 90%;
  margin-left: -100px; /* Half of the tooltip width */
  opacity: 0.8;
}

 .tooltip:hover .tooltip-text {
  visibility: visible;
}


 

  

  